@mixin button {
    display: inline-block;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 1em;
    color: #555;
    border-radius: $border-radius;
    background-color: transparent;
    font-family: $font-family;
    transition: $transition-1;
    &:hover {
        background: rgba(#000, 0.05);
    }
    &:active {
        background: rgba(#000, 0.1);
        box-shadow: inset 0 2px 10px rgba(#000,0.25);
    }
}

@mixin ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

@mixin modal {
    position: fixed;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0 auto;
    background: #fff;
    border-radius: $border-radius;
    box-shadow: $shadow-2;
    min-width: 300px;
    max-width: 100%;
    z-index: 8;
}

@mixin close {
    @include button;
    right: 2px;
    top: 2px;
    position: absolute;
}

@mixin overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#000,0.1);
    z-index: 7;
}